.hamburger {
  background: none;
  display: block;
  cursor: pointer;
  border: 3px solid #e9eae2;
  transition-duration: 500ms;
  transition-property: border-color;
}
.hamburger:hover, .hamburger:active {
  border-color: #f8c255;
}
.hamburger:hover .hamburger__element, .hamburger:active .hamburger__element {
  background-color: #f8c255;
}
.hamburger:hover .hamburger__element::after, .hamburger:hover .hamburger__element::before, .hamburger:active .hamburger__element::after, .hamburger:active .hamburger__element::before {
  background-color: #f8c255;
}
.hamburger__element {
  width: 40px;
  height: 3px;
  background-color: #e9eae2;
  display: block;
  margin: 0 auto;
  transition-duration: 500ms;
  transition-property: background-color;
}
.hamburger__element::before, .hamburger__element::after {
  content: "";
  width: 40px;
  height: 3px;
  background-color: #e9eae2;
  display: block;
  margin: 0 auto;
  transition-duration: 500ms;
  transition-property: background-color;
}
.hamburger__element::before {
  transform: translateY(-7px);
}
.hamburger__element::after {
  transform: translateY(4px);
}
.hamburger_active {
  position: relative;
  z-index: 1;
  border-color: #f8c255;
}
.hamburger_active:hover .hamburger__element, .hamburger_active:active .hamburger__element {
  background-color: #e9eae2;
}
.hamburger_active:hover .hamburger__element::after, .hamburger_active:active .hamburger__element::after {
  background-color: #e9eae2;
}
.hamburger_active .hamburger__element {
  transform: rotate(45deg);
}
.hamburger_active .hamburger__element::after {
  transform: rotate(-90deg);
  position: relative;
  z-index: 1;
}
.hamburger_active .hamburger__element::before {
  display: none;
}

.menu {
  position: absolute;
  right: 11%;
  top: 50px;
}
.menu__hamburger {
  width: 119px;
  height: 114px;
}
.menu__list {
  background-color: #f8c255;
  list-style: none;
  margin: 0;
  padding: 0;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  min-width: 725px;
  padding-right: 119px;
  padding-left: 25px;
  box-sizing: border-box;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;
  display: none;
  z-index: -5;
}
.menu__list_active {
  display: flex;
  z-index: 1;
  animation: menu 500ms;
}
.menu__link {
  text-decoration: none;
  font-size: 14px;
  line-height: 1.786;
  padding: 30px;
  text-transform: uppercase;
  color: #fff;
  transition-duration: 500ms;
  transition-property: color;
}
.menu__link:hover {
  color: #000;
}
@media (min-width: 1500px) {
  .menu__link {
    /*
    $minSizePercent is a (local font size / global font size) * 100%;
    $maxSize is a (local font size * 2);
    $minSize is a local font;
    $minWidth is a min-width in px;
    $maxWidth is a (min-width * 2);
    $minWidth is a min-width;
    */
    font-size: calc(87.5% + (28 - 14) * (100vw - 1500px) / (3000 - 1500));
  }
}

@keyframes menu {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@media (min-width: 1500px) {
  .menu__list {
    min-width: 1100px;
  }
}
@media (max-width: 980px) {
  .menu {
    top: 30px;
    right: 30px;
  }
}
@media (max-width: 768px) {
  .menu {
    height: 100%;
  }
  .menu__hamburger {
    width: 70px;
    height: 70px;
  }
  .menu__list_active {
    display: block;
    min-width: auto;
    padding-right: 70px;
    padding-top: 15px;
    overflow-y: hidden;
  }
  .menu__link {
    display: block;
    padding: 7px 80px 7px 0;
  }
}
